//Search
#search_results	{
	padding: 0rem;
	padding-bottom: 1rem;
}
#search_results > .container {
	background: @fp-background-white-normal;
	color: @fp-text-normal;
	margin-bottom: 5em;
	margin: 0 auto;
	position: relative;
	padding: 1rem;
}
.page-search {
	.searchentry {
		.searchheader A {
			color: @fp-text-link-normal;
			&:hover {
				color: @fp-text-link-light;
			}
		}
		.searchmeta {
			color: @fp-text-light;
		}
		.searchbody {
			.highlighted {
				color: @fp-text-highlight;
				  font-weight: bolder;
			}
			overflow-wrap: break-word;
		}
	}
}

.menu-label {
	color: @fp-text-normal;
	border-bottom: 1px solid @fp-background-grey-dark;
	font-weight: bold;
}
.menu-list {
	a.is-active {
		background-color: @fp-background-blue-normal;
		color: @fp-text-lighter;
	}
	li a {
		color: @fp-text-normal;
		&:hover {
			color: @fp-text-lighter;
		}
	}
	.count {
		background-color: @fp-background-blue-normal;
		color: @fp-text-lighter;
	}
}